.report{
    width: calc(100% - 285px);
    height: 318px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 1px 8px rgba(128, 128, 128, 0.4);
    margin-left: 10px;
    overflow-y: hidden;
    padding-bottom: 40px;
}
.report::-webkit-scrollbar {
    width: 8px;
}
.report > .scroll-cover {
    position: absolute;
    top: 0;
    right: calc(100% - 297px);
    width: 8px;
    height: 100%;
    background-color: white;
    border-radius: 15px 0 0 15px;
    transition: 0.5s width;
}
.report:hover.report > .scroll-cover {
    width: 0;
}
.report:hover.report {
    overflow-y: auto;
}
.report::-webkit-scrollbar-track {
    background: #f1f1f1;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
    border-radius: 15px 0 0 15px;
}
.report::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(to right, #6494b35d,#64ceff,#6494b35d); 
}
.report::-webkit-scrollbar-thumb:hover {
    background: rgb(118, 150, 255); 
}
.report > p{
    font-size: 1rem;
    font-weight: bold;
    color: #333;
    text-align: center;
    position: sticky;
    top: 0;
    line-height: 60px;
    background: linear-gradient(to bottom, white 50%, transparent);
    margin-bottom: -10px;
}
.report-list {
    width: calc(100% - 60px);
    padding: 0 5px;
    margin: 10px 30px;
    border-radius: 30px;
    height: 50px;
    background-color: #eee;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #ddd;
}
.report-list:first-child {
    background-color: #111;
}
.report-list > div {
    display: flex;
    align-items: center;
}
.report-data {
    width: 100%;
    overflow: hidden;
}
.report-icon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    padding: 6px;
    background-color: #fef;
}
.report-title {
    width: 100%;
    margin: 0 15px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.progress-span {
    width: 150px;
    height: 10px;
    border-radius: 30px;
    background-color: #eee;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
    display: flex;
    margin-left: 10px;
}
.progress-span > .progress-value {
    border-radius: 30px;
    background-image:
        -webkit-linear-gradient(-45deg, transparent 33%, rgba(0, 0, 0, .1) 33%, rgba(0,0, 0, .1) 66%, transparent 66%),
        -webkit-linear-gradient(top, rgba(255, 255, 255, .25), rgba(0, 0, 0, .25)),
        -webkit-linear-gradient(left, #09c, #f44);
    background-size: 35px 20px, 100% 100%, 100% 100%;
    transition: 1s width;
}
.report-progress {
    margin-left: 20px;
}
.report-progress > p {
    font-size: 0.8rem;
}

@media only screen and (max-width: 400px) {
    .report-list {
        border-radius: 25px;
        height: 100px;
        flex-direction: column;
        justify-content: flex-start;
    }
    .report-icon {
        margin-top: 5px;
    }
    .report-progress {
        margin: 10px 0;
    }
}

@media only screen and (max-width: 700px) {
    .report{
        width: 100%;
        border-radius: 15px;
        margin: 0;
        overflow-y: auto;
    }
    .report > .scroll-cover {
        display: none;
    }
    .report::-webkit-scrollbar-track {
        background: #f1f1f1;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
        border-radius: 25px 0 0 25px;
    }
}