header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
}
.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: 40px;
}
.logo > div:first-child {
  display: flex;
}
.Menubar {
  display: none;
}
.logo-image {
  width: 38px;
  margin: 0 7px;
  float: right;
}
.user-select {
  display: inline-block;
}
.user-select > p {
  list-style: none;
  border-radius: 30px;
  border: none;
  background-color: white;
  padding: 5px 15px;
  background-color:#fff;
  box-shadow:0 1px 4px rgba(128, 128, 128, 0.3);
  font-size: 0.9rem;
  cursor: pointer;
}
.user-select > p > i {
  margin-right: 10px;
  color:#99ccff;
  font-size: 10px;
  transition: transform 0.5s;
}
.user-names{
  height: 0;
  margin-top: 1px;
  width: 151px;
  position: absolute;
  background-color: #fff;
  box-shadow:0 1px 4px rgba(128, 128, 128, 0.3);
  border-radius: 15px;
  z-index: 1;
  overflow: hidden;
  transition: height 0.5s;
}
.user-names-open {
  height: 126px;
}
.user-names > button {
  background-color: transparent;
  border: none;
  text-align: right;
  width: 100%;
  padding: 10px 5px;
  cursor: pointer;
  font-size: 0.9rem;
}
.user-names > button:hover {
  background-color: #e0e0e0;
}
/* .user-select:hover .user-names{display: block;} */
.profile-pic {
  width: 20px;
  height: 20px;
  float: right;
  margin: 0 10px;
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
  border-radius: 50%;
  border: 1px dashed;
}
.logo > input {
  border-radius: 30px;
  border: none;
  width: 80px;
  padding: 3px 10px;
}
input::-webkit-calendar-picker-indicator {
  opacity: 100;
  color: #15a4fa;
}
.options{
  width: fit-content;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.options > a {
  width: 110px;
  margin-right: 15px;
  padding: 5px 15px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  color: #333;
  border-radius: 50px;
  background-color:#fff;
  box-shadow:0 1px 4px rgba(128, 128, 128, 0.3);
  display: flex;
  align-items: center;
}
.options > a:hover {
  background-color: #46b7fd;
  color: white;
}
.options > a:last-child:hover {
  background-color:#ff6b6b;
  color: white;
}
.options > a > i{
  margin-left: 10px;
  font-size: 1rem;
}
.options > a:hover.options > a > i {
  color: white;
}
.options > a > p {
  font-size: 0.9rem;
}
.options > a:nth-child(1) > p {
  width: 50px;
}
.options > a:nth-child(3) > p {
  width: 45px;
}

@media only screen and (max-width: 1000px) {
  .Menubar {
    cursor: pointer;
    display: block;
    color: rgba(0, 0, 0, 0.5);
    font-size: 20px;
    font-weight: 900;
    background-color: #fff;
    padding: 9px 10px;
    border-radius: 50%;
    box-shadow:0 1px 4px rgba(128, 128, 128, 0.4);
  }
}

@media only screen and (max-width: 700px) {
  header {
    flex-direction: column;
  }
  .logo{
    width: 100%;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .options{
    width: fit-content;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .options > a:first-child {
    margin: 0;
  }
}
@media only screen and (max-width: 400px) {
  .options > a {
    width: 100px;
    margin-right: 5px;
  }
  .options > a > p {
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 340px) {
  .logo {
    margin-bottom: 0;
  }
 .options {
   display: none;
 }
 .user-select > p {
   font-size: 0.8rem;
 }
}