.Menu {
    width: 220px;
    height: 1000px;
    margin-top: 20px;
    transition: 0.5s;
}
.sticky-menu{
    position: sticky;
    top: 20px;
    width: calc(100% - 20px);
    height: 450px;
    margin: 5px 10px;
    background: #fff;
    border-radius: 15px;
    overflow: hidden;
    white-space: nowrap;
    box-shadow:0 1px 8px rgba(128, 128, 128, 0.25);
}
.closeMenu{
    display: none;
    position: fixed;
    left: 20px;
    top: 20px;
    color: white;
    text-shadow: 0 1px 4px rgba(51, 51, 51, 0.5);
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 1px 4px rgba(51, 51, 51, 0.2);
    padding: 8px 11px;
    border-radius: 50%;
    text-align: center;
}
.sticky-menu > a{
    display: block;
    width: 100%;
    padding: 15px 10px;
    text-align:right;
    cursor:pointer;
    color: #333;
    text-decoration:none;
    font-size: 1rem;
}
.sticky-menu > a:hover{
    background-color:#eee;
}
.sticky-menu > a > i{
    margin-right: 8px;
    margin-left: 16px;
    color:#6cb2f8;
    font-size: 1.2rem;
    width: 25px;
    text-align: center;
}

@media only screen and (max-width: 1000px) {
    .Menu {
        position: fixed;
        width: 0;
        height: 100%;
        top: 0;
        right: 0;
        margin: 0;
    }
    .sticky-menu{
        margin: 0;
        width: 100%;
        height: 100%;
        background: #6cb2f893;
        backdrop-filter: blur(8px);
        -webkit-backdrop-filter: blur(8px);
        padding-top: 60px;
        border-radius: 0;
    }
    .sticky-menu > a{
        display: block;
        width: 80%;
        padding: 10px 0;
        font-size: 20px;
        color: white;
        text-shadow: 0 1px 4px rgba(51, 51, 51, 0.5);
        border-bottom: 1px solid rgba(238, 238, 238, 0.8);
        margin: 0 10%;
    }
    .sticky-menu > a:hover{
        background-color:#ff00ea28;
    }
    .sticky-menu > a > i{
        margin: 0 20px;
        color: white;
    }
}