.progress-div{
    position: absolute;
    bottom: 5px;
    left: 0;
    width: 60%;
    height: calc(100% - 260px);
    background-color: #fff;
    box-shadow:0 1px 8px rgba(128, 128, 128, 0.4);
    float: right;
    border-radius:10px;
    text-align: center;
}
.progress-div > p {
    margin-top: 15px;
    font-size: 1rem;
    font-weight: bold;
    color: #333;
}
.lineChart {
    position: relative;
    height: 270px;
    margin: 10px;
}
.lineChart * {
    direction: ltr;
}
.chartContainer {
    width: 100%;
    height: 100%;
    margin: 0 auto;
}
HighchartsReact > * {
    font-size: 8px;
}

@media only screen and (max-width: 700px) {
    .progress-div{
        position: static;
        width: 100%;
        height: 280px;
        box-shadow: 0 2px 10px #ccc;
        border-radius: 15px;
        float: none;
    }
    .progress-div > p {
        padding-top: 10px;
    }
    .lineChart {
        position: relative;
        height: 230px;
        margin: 10px;
    }
}

@media only screen and (max-width: 400px) {
    .progress-div {
        height: 250px;
    }
    .lineChart {
        position: relative;
        height: 200px;
        margin: 10px;
    }
}