.userdiv{
    width: calc(40% - 15px);
    box-shadow:0 1px 8px rgba(128, 128, 128, 0.4);
    background-color: white;
    float: right;
    border-radius:10px;
    margin: 5px 10px;
    margin-right: 0;
}
#profile-image {
    background-color:#fff;
    margin: auto;
    padding: 5px 10px;
    padding-top: 10px;
    border-radius: 10px 10px 0 0;
    text-align:center;
    border-bottom: 1px solid #ddd;
}
#profile-image > img {
    width: 90px;
    display: block;
    margin: 15px auto;
}
#profile-image > p {
    margin-bottom: 10px;
    font-size: 1.2rem;
}
.userdata > p {
    padding:  10px;
    margin: 0 20px;
    color:black;
    border-bottom: 1px solid #ddd;
    text-align: right;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.userdata > p > i {
    font-size: 0.8rem;
    margin-left: 15px;
}
.icon-state {
    margin-right: 5px;
}
.fa-lock.icon-state {
    color: rgba(16, 190, 16, 0.63);
}
.fa-unlock.icon-state {
    color: rgba(255, 0, 0, 1);
}
.buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
}
.buttons > a {
    border-radius:30px;
    padding: 4px 15px;
    text-decoration: none;
    white-space: nowrap;
    display: flex;
    align-items: center;
}
.buttons > a > i {
    margin-left: 10px;
    float: right;
}
.buttons > a > p {
    font-size: 0.8rem;
    float: right;
}
#logout{
    border: 1px solid #ff6b6b;
    background-color:#fff;
    margin-left: 10px;
    color: #ff5050;
}
#logout:hover{
    background-color:#ff6b6b;
    color: white;
}
#edit{
    border: 1px solid #46b7fd;
    background-color:white;
    color: #727cf5;
}
#edit:hover{
    background-color:#46b7fd;
    color: white;
}

@media only screen and (max-width: 700px) {
    .userdiv{
        float: none;
        width: 100%;
        margin: 5px auto;
        border-radius:15px;
    }
    #profile-image {
        border-radius: 15px 15px 0 0;
    }
    #profile-image > p {
        margin-top: 10px;
        font-size: 1.2rem;
    }
    .userdata > p{
        color:black;
        border-bottom: 1px #ddd;
        text-align: right;
        font-size: 1rem;
        border-style:none none solid none;
    }
    .buttons {
        padding-top: 0;
        margin-bottom: 20px;
    }
}