.datadiv {
    width: 60%;
    float: right;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 5px;
    margin-right: 4px;
}
.datadiv > div{
    width: calc(50% - 5px);
    height: 240px;
    box-shadow:0 1px 8px rgba(128, 128, 128, 0.4);
    display: inline-block;
    text-align: center;
    border-radius:10px;
}
.datadiv > div:first-child {
    background: linear-gradient(to bottom, #3399ff 0%, #00ccff 80%);
}
.datadiv > div:last-child {
    background: linear-gradient(to bottom, #28b779 0%, #28d779 80%);
}
.datadiv > div > p {
    margin-top: 20px;
    font-size: 1rem;
    font-weight: bold;
    color:#efe;
}

@media only screen and (max-width: 700px) {
    .datadiv {
        width: 100%;
        margin-top: -5px;
        float: none;
    }
    .datadiv > div{
        width: calc(50% - 5px);
        border-radius: 15px;
    }
}

@media only screen and (max-width: 400px) {
    .datadiv {
        flex-direction: column;
    }
    .datadiv > div {
        width: 100%;
    }
    .datadiv > div:first-child {
        margin-bottom: 15px;
    }
}

.pie-chart{
    position: relative;
    text-align: center;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    margin: 20px auto;

    background-color:#39B4CC;
    background-image:
        linear-gradient(91deg, transparent 50%, #A2ECFB 50%),
        linear-gradient(90deg, #A2ECFB 50%, transparent 50%);
}
.circle{
    position: relative;
    top: 5px;
    left: -5px;
    text-align: center;
    width: 120px;
    height: 120px;
    border-radius: 100%;
    background-color: #E6F4F7;
}
.prec{
    font-family: vazir;
    top: 42px;
    position: relative;
    font-size: 1.2rem;
}