.second-div {
    float: left;
    width: calc(100% - 240px);
    margin-top: 25px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
}
.report-cover {
    width: calc(100% - 296px);
    height: 20px;
    background: linear-gradient(to top, white -10%, transparent);
    position: absolute;
    bottom: 0;
    border-radius: 0 0 15px 15px;
}
.slides {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 280px;
}
.slides > div {
    background-color:white;
    width: 100%;
    height: 100px;
    box-shadow:0 1px 8px rgba(128, 128, 128, 0.3);
    border-radius: 10px;
}
.slides > div:hover {
    background-color:#fef;
}
.slides > div > a > img{
    width: 40%;
    height: 100%;
    border-radius:0 10px 10px 0;
    float: right;
}
.achievementData{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;
    height: 100px;
    color: rgb(100, 100, 100);
    float: right;
    padding: 10px;
}
.achievementData > p {
    text-align: center;
    line-height: 30px;
    font-size: 0.8rem;
}

@media only screen and (max-width: 1000px) {
    .second-div {
        width: 100%;
    }
}

@media only screen and (max-width: 700px) {
    .second-div {
        width: 100%;
        margin-top: 30px;
        margin-bottom: 100px;
        flex-direction: column;
    }
    .report-cover {
        width: calc(100% - 8px);
        border-radius: 0 0 15px 15px;
        top: 298px;
    }
    .stat{
        width: 100%;
        border-radius: 15px;
        margin: 0;
    }
    .slides {
        margin: 15px 0;
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .slides > div {
        width: calc(50% - 5px);
        margin-bottom: 10px;
        border-radius: 15px;
    }
    .slides > div > a > img{
        border-radius:0 15px 15px 0;
    }
}

@media only screen and (max-width: 400px) {
    .slides > div {
        width: 100%;
        height: 120px;
    }
    .achievementData > p{
        font-size: 1rem;
    }
}